import React from 'react';
import AppBar from '@material-ui/core/AppBar';
import Typography from '@material-ui/core/Typography';
import VideogameAssetIcon from '@material-ui/icons/VideogameAsset';
import './navbar.css';

import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import Grid from '@material-ui/core/Grid';
import SearchIcon from '@material-ui/icons/Search';
import ShoppingCartIcon from '@material-ui/icons/ShoppingCart';
import { useStateValue } from "../StateProvider";
import { Link } from "react-router-dom";
import { auth } from "../firebase";


export default function NavBar() {
    const [{ basket, user }, dispatch] = useStateValue();

    const handleAuthenticaton = () => {
        if (user) {
          auth.signOut();
        }
      }


    return (
        <AppBar id='nav-bar'>
        <Grid container  alignItems="center" justify='flex-start'>
            <Grid item xs={12} md={2}>
            <Link to="/" className='link'>
                <VideogameAssetIcon id='logo' />
            </Link>
            </Grid>
            <Grid item xs={10} md={3} container alignContent='center' alignItems='center'>
                    <TextField fullWidth id="input-search" />
            </Grid>
            <Grid item xs={2}  container alignContent='center' alignItems='center'>
                <SearchIcon />
            </Grid>
            <Grid item xs={6} md={4} container alignItems='center'>
                <Grid item xs={6}>
                <Typography>Welcome, {!user ? 'Stranger' : user.email}!</Typography>
                </Grid>
                <Grid item xs={6} md={3}>
                    <Link to={!user && '/login'} id='login-menu'>
                    <div onClick={handleAuthenticaton} className="header__option">
                        <Button id='sign-in'>{user ? 'Sign Out' : 'Sign In'}</Button>
                    </div>
                    </Link>
                </Grid>
            </Grid>
            <Grid item xs={1} md={1} >
            <Link to="/checkout" className='link'>
                <ShoppingCartIcon id='cart'  />
                <span>{basket?.length}</span>
            </Link>
            </Grid>
        </Grid>
        </AppBar>
    )
}