import React, {useState} from 'react';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import Button from '@material-ui/core/Button';
import { Typography } from '@material-ui/core';



export default function UnderDialog() {

    const [open, setOpen] = useState(true);

    const handleClose = () => {
      setOpen(false);
    };

    return (
<div>
      <Dialog
        open={open}
        onClose={handleClose}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogTitle id="alert-dialog-title">Hello, Stranger! And welcome to my ecommerce!</DialogTitle>
        <DialogContent>
          <DialogContentText id="alert-dialog-description">
            <p>This app is currently under development. But for now you will be able to:</p>
            <ul>
                <li>Create a user and login</li>
                <li>Add products to your cart</li>
                <li>Proceed to checkout</li>
                <li>See the total amount of your order</li>
            </ul>
            <p>Please, return next week to see it's full version!</p>
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose} color="primary">
            Lets's see what you got
          </Button>
        </DialogActions>
      </Dialog>
    </div>
    )
}